import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import acrylic from '@/components/acrylic'
import moth from '@/components/moth'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/acrylic',
      name: 'acrylic',
      component: acrylic
    },
    {
      path: '/moth',
      name: 'moth',
      component: moth
    }
  ]
})



